export default {
	data() {
		return {
			carStyle: {
				"direction": 'column'
			},
			joinTimes: 0,
			imgUrl: 'http://106.15.194.58/images/', //图片接口
			dayCur: -1, //日期选中状态
			buyList: [], //历史购买
			worship: [ //礼拜天数
				{
					name: "星期一"
				},
				{
					name: "星期二"
				},
				{
					name: "星期三"
				},
				{
					name: "星期四"
				},
				{
					name: "星期五"
				}
			],
			dataId: '', // 日期id
			category: [], //左侧菜单类别
			goods: [], //合并后总数据
			// foods: [],  //右侧数据
			height: 0,
			foodSTop: 0,
			currentIndex: 0
		}
	},
	components: {
		shopcart,
